.nav{
    position: fixed;
    top: 0;
    padding: 15px;
    width: 100%;
    height: 10%;
    z-index: 1;

    /* Animations */
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
.nav__black{
    background-color: black;
}
.nav__contents{
display: flex;
}
.nav__logo{
    position: fixed;
    width: 12.5%;
    left: 0;
    padding-top: 0.5%;
    cursor: pointer;
    padding-left: 20px;
}
.nav__avatar{
    position: fixed;
    right: 20px;
    width: 5%;
    cursor: pointer;
}